import React, { useEffect, useState } from "react"
import './product.scss';

function ProductView({ product }) {
  const [calculatedPrice, setCalculatedPrice] = useState(product.price)
  const [options, setOptions] = useState({
    frame: 'Black',
    glass: 'UV70',
    size: '8x6',
    thickness: 'Skinny',
  });

  const updateOptions = (event) => {
    // console.log(event.target, event.tar)
    setOptions({
      ...options,
      [event.target.name]: event.target.value,
    })
  }


  useEffect(() => {
    const additions = (
      variationsAdditions.frame[options.frame] + variationsAdditions.glass[options.glass] + variationsAdditions.thickness[options.thickness]
    )
    setCalculatedPrice(product.price + additions)
  }, [options])

  const variationsAdditions = {
    frame: {
      "Black": 0,
      "White": 0,
      "Slate": 5,
      "Rimu": 0
    },
    glass: {
      "Standard": 0,
      "UV70": 25
    },
    thickness: {
      "Skinny": 0,
      "Slim": 5,
      "Standard": 5
    }
  }

  return (
      <main>
        <div className="spacer"></div>
        <div className="simple-grid">
          <div className="simple-grid__400">
            <img className="product__image" alt={product.description} src={`https://bmp-images.s3.ap-southeast-2.amazonaws.com/${product.meta.sku}/${options.size}/${options.frame}${options.thickness}.png`} />
          </div>
          <div className="simple-grid__fill">
            <h1>{product.name}</h1>
            <p>{product.description}</p>
            <p>
              Please note: Not <em>EVERY</em> frame colour comes in every thickness!
              Until we customise the way this page works please accept that if the
              image of the print disappears that we cannot sell that combination.
              For ease of use: Slate is not available in Slim and Rimu is not available
              in Standard.
            </p>      
            <p>${calculatedPrice}</p>
            <label>
              Frame colour
              <select name="frame" id="frame" onChange={updateOptions} value={options.frame}>
                <option>Select one...</option>
                <option value="Black">Black</option>
                <option value="Rimu">Rimu</option>
                <option value="Slate">Slate</option>
                <option value="White">White</option>
              </select>
            </label>
            <label>
              Glass type
              <select name="glass" id="glass" onChange={updateOptions} value={options.glass}>
                <option>Select one...</option>
                <option value="Standard">Standard</option>
                <option value="UV70">UV70</option>
              </select>
            </label>
            <label>
              Print size
              <select name="size" id="size" onChange={updateOptions} value={options.size}>
                <option>Select one...</option>
                <option value="8x6">8" x 6"</option>
              </select>
            </label>

            <label>
              Frame thickness
              <select name="thickness" id="thickness" onChange={updateOptions} value={options.thickness}>
                <option value="Standard">Standard (30mm wide x 20mm deep)</option>
                <option value="Skinny">Skinny (20mm wide x 20mm deep)</option>
                <option value="Slim">Slim (20mm wide x 30mm deep)</option>
              </select>
            </label>


            <button className="snipcart-add-item"
              data-item-id={product.meta.id}
              data-item-price={product.price}
              data-item-description={product.description}
              data-item-image={`https://bmp-images.s3.ap-southeast-2.amazonaws.com/${product.meta.sku}/${options.size}/${options.frame}${options.thickness}.png`}
              data-item-name={product.name}
              data-item-weight={options.thickness === 'Standard' ? product.meta.weight + 40 : product.meta.weight }
              data-item-custom1-type="readonly"
              data-item-custom1-name="Frame color"
              data-item-custom1-options="Black|White|Rimu|Slate[+5]"
              data-item-custom1-value={options.frame}
              data-item-custom2-name="Glass type"
              data-item-custom2-options="Standard|UV70[+25]"
              data-item-custom2-type="readonly"
              data-item-custom2-value={options.glass}
              data-item-custom3-name="Size"
              data-item-custom3-options="8x6"
              data-item-custom3-type="readonly"
              data-item-custom3-value={options.size}
              data-item-custom4-name="Thickness"
              data-item-custom4-options="Standard[+5]|Skinny|Slim[+5]"
              data-item-custom4-type="readonly"
              data-item-custom4-value={options.thickness}
            >
              Add to cart
            </button>
            <button className="snipcart-checkout">Click here to checkout</button>
          </div>
        </div>

      </main>
  )
}

export default ProductView